body {
  min-width: 900px;
  margin: 0;
  font-family: "Roboto", "Tahoma", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
  background-color: #d0dee9; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.btn {
  padding: 10px 15px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  color: #01b9ff;
  border: 2px solid #01b9ff;
  border-radius: 5px;
  background-color: #fff;
  transition: 0.3s ease; }
  .btn:hover {
    background-color: #01b9ff;
    color: #fff; }
  .btn:active {
    background-color: #0082b4; }

.store {
  max-width: 1200px;
  margin: auto;
  padding: 20px 20px 100px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.store__products {
  width: 72%; }

.store__basket {
  width: 25%; }

.products__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.products__item {
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px; }
  .products__item:not(:last-child) {
    margin-bottom: 20px; }
  .products__item--hide {
    display: none; }

.products__img {
  min-width: 150px;
  margin-right: 20px;
  border-radius: 5px;
  box-shadow: 0 0 5px 0px #00000036;
  overflow: hidden; }

.products__description {
  max-width: 350px;
  margin-right: 20px; }

.products__title {
  margin: 0;
  font-size: 20px; }

.products__price {
  margin: 10px 0;
  color: #01b9ff;
  font-size: 26px;
  font-weight: 700; }

.products__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  color: #9a9a9a; }

.products__add {
  margin-left: auto; }

.add__message {
  display: none;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700; }
  .add__message--count {
    color: #01b9ff; }
  .add__message--active {
    display: block; }

.add {
  align-self: flex-start; }

.basket {
  background-color: #fff;
  border-radius: 5px; }

.basket__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 15px; }

.basket__item {
  position: relative;
  display: flex;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #f3f8fb;
  border-radius: 5px;
  font-size: 16px; }

.basket__item-del {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 14px;
  line-height: 14px;
  color: #9a9a9a;
  border: none;
  background-color: transparent;
  outline: none; }
  .basket__item-del:hover {
    color: #000; }

.basket__title {
  margin: 0 15px; }

.basket__count {
  margin: 0 15px 0 auto; }

.basket__total {
  margin: 0;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center; }

.basket__sum {
  margin: 0;
  font-size: 28px;
  font-weight: 700;
  line-height: 32px;
  text-align: center;
  color: #01b9ff; }

.basket__btn {
  display: block;
  max-width: 200px;
  margin: 20px auto;
  text-decoration: none;
  text-align: center;
  color: #fff;
  background-color: #01b9ff; }
  .basket__btn:hover {
    background-color: #0095cd; }
  .basket__btn:active {
    background-color: #0082b4; }

.basket__empty {
  font-size: 30px;
  font-weight: 700;
  color: #26323854;
  text-align: center; }
  .basket__empty--hide {
    display: none; }

.pagination {
  display: flex;
  margin-top: 20px; }

.pagination__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin: 0 5px; }

.pagination__btn,
.pagination__link {
  min-width: 34px;
  height: 34px;
  border-radius: 3px;
  background-color: #fff; }

.pagination__btn {
  font-size: 20px;
  border: none; }

.pagination__item {
  margin: 0 5px; }

.pagination__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #000;
  font-size: 14px;
  font-weight: 700; }
  .pagination__link--active {
    background-color: #01b9ff;
    color: #fff; }

.filter {
  max-width: 1200px;
  margin: auto;
  padding: 50px 20px 0;
  display: flex;
  align-items: flex-start; }

.filter__btn {
  display: flex;
  align-items: center;
  margin-right: 10px; }
  .filter__btn svg {
    margin-left: 10px;
    stroke: #01b9ff;
    transition: stroke 0.3s ease; }
  .filter__btn:hover, .filter__btn--active {
    background-color: #01b9ff;
    color: #fff; }
    .filter__btn:hover svg, .filter__btn--active svg {
      stroke: #fff; }
  .filter__btn--up svg {
    transform: rotate(180deg); }
